body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(252.44deg, #EF5DA8 0%, rgba(239, 93, 168, 0) 100%), #5D5FEF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.board {
  position: absolute;
  width: 500px;
  height: 500px;
  left: 55%; 
  top: 20%;
  font-size:0px;
}

.tile {
  position:absolute;
  background-color: transparent;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: none;
  font-size: 0;
  mix-blend-mode: lighten;
}
.tileOuter {
  position: absolute;
  background-color: transparent;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border: none;
}
.tileInner {
  position:relative;
  font-weight: 500;
  background-color: white;
  border-radius: 6px; /*rounded edges*/
  padding: 0px;
  display: grid;
	place-items: center;
  color: 'black';
}

.gradtext {
  background: linear-gradient(209.84deg, #EF5DA8 -29.17%, rgba(239, 93, 168, 0) 130.69%), linear-gradient(0deg, #5D5FEF, #5D5FEF);
  background-clip: unset;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.topbar {
  background-color: whitesmoke;
}


.titleText {
  position: absolute;
  font-size: 160px;
  color: white;
  width: 50%;
  left: 10%;
  top: 14%;
  -webkit-animation: yourNewSuperpower 1s;
          animation: yourNewSuperpower 1s;
}
@-webkit-keyframes yourNewSuperpower {
  from {
  	opacity: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
}
@keyframes yourNewSuperpower {
  from {
  	opacity: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
  }
}

.buttonsMainPage {
  position:absolute;
  top: 75%;
  left: 18%;
  -webkit-animation: yourNewSuperpower 1s;
          animation: yourNewSuperpower 1s;
}

.button1 {
  width: 200px;
  height: 60px;
  border-radius: 6px;
  position: relative;
  font-size: 28px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border: none;
  text-align: center;
  background-color: white;
  color:#5D5FEF;
}
.button1:active {
  background: linear-gradient(252.44deg, #EF5DA8 0%, rgba(239, 93, 168, 0) 100%), #5D5FEF;
  -webkit-text-fill-color: white;
}
.button1 + .button1 {
  margin-left: 90px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

